import React, { Component } from "react";
import InnerBanner from "./components/common/innerBanner";
import ListingService from "./services/ListingService";
import TokenExpired from "./components/common/tokenExpired";
import { PictureFilled, HeartFilled } from "@ant-design/icons";

import { ToastContainer, toast } from "react-toastify";
import "antd/dist/antd.css";

import {
  AutoComplete,
  Card,
  Typography,
  Space,
  Tag,
  Tooltip,
  message,
  Avatar,
  Alert,
  Checkbox,
  Row,
  Col,
  Input,
} from "antd";
import * as constant from "./constants";
import InterestService from "./services/InterestService";

import { Link, NavLink } from "react-router-dom";
import ProfileService from "./services/ProfileService";

const { CheckableTag } = Tag;
const { Meta } = Card;

const tagsData = [
  "Height",
  "Marital Status",
  "Caste",
  "Gotra",
  "Place Of Birth",
];
class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.form = React.createRef();
    this.state = {
      profileList: null,
      profileList2: null,

      searchBoxOptions: [],
      selectedProfileName: "",
      listUpdateCount: 0,
      selectedTags: [],
      searchValue: "",
      refreshByTags: true,
      refreshByName: true,
      interestArray: [],
      checked: true,
      disabled: false,
      label: false,
      Profiledata: "",
      show: false,
      interestArray: "",
      getListBytags: "",
      desiredAgeGroup: "",
      desiredMaritalStatus: "",
      desiredCaste: "",
      desiredAgeGroup: "",
      desiredHeight: "",
      desiredGotra: "",
      desiredPlaceOfBirth: "",
      firstName: "",
      tagsSearch: "",
      profileDump: "",
    };

    //  console.log("age", this.state.firstName);
    sessionStorage.setItem("flag", 0);
    this.sliceIntoChunks = this.sliceIntoChunks.bind(this);
    this.getInterests = this.getInterests.bind(this);
    this.handleTagChange = this.handleTagChange.bind(this);
    this.options = props.options;
  }

  changeHandle = () => {
    this.setState({
      label: !this.state.label,
      show: !this.state.show,
      checked: !this.state.checked,
      disabled: this.state.disabled,
    });
    if (this.state.label) {
      this.fetchData();
    } else {
      this.fetchData1();
    }
  };

  async fetchData(
    id = sessionStorage.getItem("userId"),
    ischeck = "",
    tagDetails = null,
    profileName = ""
  ) {
    return ListingService.fetchProfilesForUser(
      id,
      (ischeck = this.state.label ? "&ischeck=1" : "&ischeck=1"),
      tagDetails,
      profileName
    )
      .then((result) => {
        // console.log("fetchData", result.data.message);

        this.setState({ profileList: result.data, profileList2: result.data });
        return result.data;
      })
      .catch((error) => {});
  }

  async fetchData1(
    id = sessionStorage.getItem("userId"),
    ischeck = this.state.label ? "&ischeck=0" : "&ischeck=0",
    tagDetails = null,
    profileName = ""
  ) {
    return ListingService.fetchProfilesForUser(
      id,
      ischeck,
      tagDetails,
      profileName
    )
      .then((result) => {
        this.setState({ profileList: result.data });
        return result.data;
      })
      .catch((error) => {
        if (error.toString().indexOf("401") === -1) {
          const style = {
            fontSize: "150%",

            padding: "2%",
            marginTop: "90%",
            color: "white",
          };

          toast(
            <div class="">
              <i class=""></i>No profiles were found for your desired
              preferences.
            </div>,
            { type: "error", position: "top-center", theme: "colored", style }
          );
        }
      });
  }

  async getInterests(id = sessionStorage.getItem("userId")) {
    try {
      let res = await InterestService.getById(id);
      this.setState({ interestArray: res.data.interest });

      return res.data;
    } catch (e) {
      return e;
    }
  }
  sliceIntoChunks(arr, chunkSize) {
    const res = [];
    for (let i = 0; i < arr.length; i += chunkSize) {
      const chunk = arr.slice(i, i + chunkSize);
      res.push(chunk);
    }
    return res;
  }

  componentDidMount() {
   
    if (localStorage.getItem("flag") && localStorage.getItem("flag") <= 365) {
      const style = {
        fontSize: "20px",
        padding: "2%",
        marginTop: "70%",
        backgroundColor: "darkorange",
        color: "white",
      };

      toast(
        <div class="container-fluid">
          <div class="row">
            {" "}
            Your profile is going to Expire within 15 days !{" "}
          </div>
        </div>,

        { position: "top-center", style }
      );
      localStorage.removeItem("flag");
    }

    window.scrollTo(0, 0);

    if (sessionStorage.getItem("status") == "ACTIVE") {
      this.fetchData();
      this.getInterests();
    }

    
    this.setState({ profileList: this.state.getListBytags });
  }
 
  getListByTags = async () => {
    let tagDetails = {
      id: sessionStorage.getItem("userId"),
      gender: localStorage.getItem("profileGender"),
      desiredHeight: localStorage.getItem("desiredHeight"),
      desiredAgeGroup: localStorage.getItem("desiredAgeGroup"),
      desiredMaritalStatus: localStorage.getItem("desiredMaritalStatus"),
      desiredCaste: localStorage.getItem("desiredCaste"),
      desiredGotra: localStorage.getItem("desiredGotra"),
      showHeight: this.state.selectedTags?.includes("Height") ? true : false,
      showMaritalStatus: this.state.selectedTags?.includes("Marital Status")
        ? true
        : false,
      showGotra: this.state.selectedTags?.includes("Gotra") ? true : false,
      showCaste: this.state.selectedTags?.includes("Caste") ? true : false,
      showplaceOfBirth: this.state.selectedTags?.includes("Place Of Birth")
        ? true
        : false
        ? true
        : false,
    };
    let id = sessionStorage.getItem("userId");
    let ischeck = "";
    let profileName = "";
    ListingService.fetchProfilesForUser(id, ischeck, tagDetails, profileName)
      .then((result) => {
         console.log(result);
        if (result.data[0].profileId) {
          this.setState({ profileList: result.data });
          if (result.data) {
            this.setState({ profileList: result.data });
          }
        } else {
          this.setState({ profileList: [] });
        }
      })
      .catch((err) => {});
  };

  componentDidUpdate(prevProps, prevState) {
    if (
      this.state.selectedProfileName !== prevState.selectedProfileName &&
      this.state.refreshByName
    ) {
    }
    if (
      this.state.selectedTags !== prevState.selectedTags &&
      this.state.refreshByTags
    ) {
      this.getListByTags();
      this.setState({
        searchValue: "",
        selectedProfileName: this.state.profileList,
        refreshByName: false,
      });
    }
  }

  handleTagChange(tag, checked) {
    if (localStorage.getItem("profileGender") !== "") {
    }

    if (!this.state.refreshByTags) {
      this.setState({
        refreshByTags: true,
      });
    }
    const { selectedTags } = this.state;
    const nextSelectedTags = checked
      ? [...selectedTags, tag]
      : selectedTags?.filter((t) => t !== tag);
    this.setState({ selectedTags: nextSelectedTags });
  }

  render() {
    const { Text } = Typography;
    let optionsArray = [];
    let cardArray = [];

    let searchSpace;
    let { listUpdateCount, interestArray } = this.state;
    const { selectedTags } = this.state;

    if (
      this.state.profileList &&
      this.state.profileList.message !== "Profile Not found"
    ) {
      this.state.profileList
        .filter((profile) => {
          if (this.state.searchBoxOptions == "") {
            return profile;
          } else if (
            profile.firstName
              .toLowerCase()
              .includes(this.state.searchBoxOptions.toLowerCase()) +
            profile.lastName
              .toLowerCase()
              .includes(this.state.searchBoxOptions.toLowerCase())
          ) {
            return profile;
          } else if (
            this.state.searchBoxOptions ===
            profile.firstName + " " + profile.lastName
          ) {
            return profile;
          }
        })
        .map(function (profile, i) {
          cardArray.push(
            <>
              <Col direction="horizontal" align="center">
                <div
                  className="col-md-4  container "
                  style={{ backgroundColor: "#E5D2BF" }}
                >
                  <Card
                    // key={i}
                    key={profile.id}
                    cover={
                      <Avatar
                        src={constant.AVATAR_PATH + profile.avatarFileName}
                        size={300}
                        style={{ padding: "4%" }}
                        icon={
                          <div>
                            <PictureFilled />
                          </div>
                        }
                        shape="square"
                      />
                    }
                    style={{
                      marginTop: "16%",

                      // backgroundColor: "darkorange",
                      color: "#401A0A",
                    }}
                    className=""
                  >
                    <div style={{ marginTop: "-10%", padding: "2%" }}>
                      <Meta
                        title={[
                          profile.firstName + " " + profile.lastName + " ",
                          interestArray?.includes(profile.userId) ? (
                            <Tooltip title={"Interest Sent"}>
                              <HeartFilled
                                style={{
                                  fontSize: "16px",
                                  // color: "#401a0a"
                                  // color:"darkorange"
                                  color: "#FF0000",
                                }}
                              />
                            </Tooltip>
                          ) : (
                            <> </>
                          ),
                        ]}
                        //  description="www.instagram.com"
                      />
                    </div>

                    <ul
                      className="profile_item"
                      style={{ backgroundColor: "#E5D2BF" }}
                    >
                      <Tooltip title={"Go to Profile"}>
                        <Link to={"/profile/" + profile.profileNumber}>
                         
                          <li
                            className="profile_item-desc "
                            style={{ textAlign: "justify" }}
                          >
                            <div className="row">
                              <Row
                                className=" col-sm-12"
                                style={{ display: "flex" }}
                              >
                                <p
                                  className=" "
                                  style={{
                                    color: "#401A0A",
                                    fontSize: "14px",
                                    paddingLeft: "4%",
                                  }}
                                >
                                  {"Age : " + profile.age} <br />
                                  {"Height : " +
                                    profile.height +
                                    " Inches"}{" "}
                                  <br />
                                  {"Education : " +
                                    profile.highestEducation}{" "}
                                  <br />
                                  {"Place Of Birth : " +
                                    profile.PlaceOfBirth}{" "}
                                  {/* +
                                    {profile.profileNumber} */}
                                  <br />
                                </p>
                              </Row>
                            </div>
                          </li>
                          <div className="clearfix"> </div>
                        </Link>
                      </Tooltip>
                    </ul>
                  </Card>
                  <br />
                </div>
              </Col>

              <ToastContainer />
            </>
          );
        });

      sessionStorage.setItem("profileName", optionsArray[0]);
      if (listUpdateCount === 0) {
        this.options = optionsArray;
      }
      let chunkedArray = this.sliceIntoChunks(cardArray, 5);

      searchSpace = (
        <>
          {chunkedArray?.map(function (profile, i) {
            return (
              <>
                <div key={i}>{profile}</div>

                <br />
              </>
            );
          })}
        </>
      );
    } else {
      searchSpace = (
        <h5 type="secondary" style={{ color: "red", textAlign: "center" }}>
          No Profiles to Show
        </h5>
      );
    }
    sessionStorage.setItem("profileName", JSON.stringify(optionsArray));

    const onChange = (data) => {
      this.setState({
        searchBoxOptions: data.target.value.trim(),
       
      });

      this.setState({
        searchValue: data,
      });
      if (data === "" || data === undefined) {
        this.setState({
          selectedProfileName: data,
          refreshByName: true,
          listUpdateCount: 0,
          searchBoxOptions: [],
          refreshByTags: true,
          selectedTags: [
            "Height",
            "Marital Status",
            "Caste",
            "Gotra",
            "Place Of Birth",
          ],
        });
      }
    };

    return (
      <>
        <TokenExpired />

        <InnerBanner />
        <div
          className="w3ls-list col-md-12 col-sm-12"
          style={{
            marginTop: "-10%",
            backgroundColor: "#E5D2BF",
            // backgroundColor:"darkorange"
          }}
        >
          <div className="container">
            <div className="col-md-12 profiles-list-agileits">
              <div className="row" style={{ marginTop: "-5%" }}>
                <div
                  className="col-xs-12"
                  style={{ display: "flex", justifyContent: "center" }}
                >
                  <Space direction="horizontal" align="center">
                    <Input
                      style={{
                        width: "300px ",
                        border: "red",
                        boxSizing: "border-box",
                        padding: "4px",
                        border: "1px solid gray",
                        textAlign: "center",
                        boxShadow: "1px,1px,2px #333333",
                        fontSize: "16px",
                        color: "black",
                        marginTop: "8%",
                      }}
                      type="text"
                      placeholder="Search Profile Names ...."
                      className="border bg-primary"
                      name="search"
                      onChange={onChange}
                    ></Input>
                  </Space>
                </div>
              </div>
              <br />
              <br />

              <div className="col-xs-12">
                <Row direction="horizontal" align="center">
                  <Tooltip title={"Age Group is mandatory"}>
                    <Tag color="#f50" className="">
                      Age Group
                    </Tag>
                  </Tooltip>
                  {tagsData.map((tag, i) => (
                    <Tooltip title={"Check/Uncheck " + tag}>
                      <CheckableTag
                        style={{
                          // color:"darkorange",
                          color: "#401A0A",
                          fontSize: "14px",
                        }}
                        key={i}
                        checked={selectedTags.indexOf(tag) > -1}
                        onChange={(checked) =>
                          this.handleTagChange(tag, checked)
                        }
                      >
                        {tag}
                      </CheckableTag>
                    </Tooltip>
                  ))}

                  <Row direction="horizontal" align="center">
                    {this.state.label ? (
                      <Checkbox
                        checked={this.state.checked}
                        onClick={this.changeHandle}
                      >
                        <label
                          style={{
                            color: "#f50",
                            // color:"#401A0A"
                          }}
                        >
                          Interested Profiles
                        </label>
                      </Checkbox>
                    ) : (
                      <Checkbox
                        checked={this.state.checked}
                        onClick={this.changeHandle}
                      >
                        <label style={{ color: "#f50" }}>
                          Show all Profiles
                        </label>
                      </Checkbox>
                    )}
                  </Row>
                </Row>
              </div>

              <br />
              <br />

              <div className="row" style={{ marginTop: "-5%" }}>
                {searchSpace}
              </div>
            </div>
            <div className="clearfix"> </div>
          </div>
        </div>
      </>
    );
  }
}

export default Dashboard;
