import React, { Component } from "react";
import TokenExpired from "./tokenExpired";
import "antd/dist/antd.css";


import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Link } from "react-router-dom";
import ProfileService from "../../services/ProfileService";
import { NavLink } from "react-router-dom";
class Header extends Component {
  constructor(props) {
    super();
    this.state = {
      logout: false,
      active: "",
    };
     this.logoutClick = this.logoutClick.bind(this);
    this.activeUser = this.activeUser.bind(this);
  }
  
  activeUser = async () => {
    ProfileService.getActivateUsers()
      .then((response) => {
        this.setState({ active: response.data.count });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  logoutClick() {
    sessionStorage.clear();
    localStorage.clear();
    this.setState({ logout: true });

    const style = { fontSize: "20px", padding: "2%", marginTop: "70%" ,backgroundColor:"darkorange",color:"white"};

    toast(
      <div class="container-fluid">
        <div class="row">You have successfully logout </div>
      </div>,

      {  position: "top-center" , style ,hideProgressBar:true }
    );
  }
  componentDidMount() {
    this.activeUser();
  }
  render() {
    // this.getActivateUser()
    let homePageHref = "/";
    let data = localStorage.getItem("true");
    console.log(sessionStorage.getItem("status"))
    localStorage.setItem("true", "true");
    if (sessionStorage.getItem("status") === "ACTIVE") {
       homePageHref = "/dashboard";
    } else if (sessionStorage.getItem("token") !== null) {
      homePageHref = "/profile";
    }
    // else if()f

    return (
      <header>
        
        <ToastContainer hideProgressBar={true} limit={1}/>
        {this.state.logout ? <TokenExpired /> : <></>}
        <div
          className="navbar"
          style={{
            background: "#e2e9ef",
            paddingBottom: "6px",
            paddingTop: "6px",
          }}
        >
          <div className="navbar-inner">
            <div className="container ">
              <a className="brand" href="https://sbatsevas.org/">
                <img
                  src="/./assets/images/logo (1).png"
                  alt=""
                  width="200"
                  height="60"
                />
              </a>
            </div>
          </div>
        </div>

        <div
          className="navbar navbar-inverse-blue navbar"
          style={{ backgroundColor: "#401A0A" }}
        >
          <div className="navbar-inner">
            <div className="container">
              <span
                style={{ color: "white", fontSize: "larger", lineHeight: 3 }}
              >
                ACTIVE PROFILES : {this.state.active}
              </span>

              <div className="pull-right">
                <nav className="navbar nav_bottom" role="navigation">
                  <div className="navbar-header nav_2">
                    <button
                      type="button"
                      className="navbar-toggle collapsed navbar-toggle1"
                      data-toggle="collapse"
                      data-target="#bs-megadropdown-tabs"
                    >
                      Menu
                      <span className="sr-only">Toggle navigation</span>
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                    </button>
                  </div>
                  <div
                    className="collapse navbar-collapse"
                    id="bs-megadropdown-tabs"
                  >
                    <ul className="nav navbar-nav nav_1">
                      <li>
                        <Link
                        // className=" button"
                          //to={homePageHref}
                          to={sessionStorage.getItem("status") === "ACTIVE"||sessionStorage.getItem("profileStatus")==="WITHDRAWN"? "/dashboard": "profile"}
                          style={{  fontSize: "larger" }}
                        >
                          HOME
                        </Link>
                      </li>
                     

                      {sessionStorage.getItem("profileStatus") == "ACTIVE"&&sessionStorage.getItem("status") == "ACTIVE" ||sessionStorage.getItem("profileStatus") == "WITHDRAWN"? (
                        <li>
                          <Link
                          className=" button"
                            to="/profile"
                            style={{ fontSize: "larger" }}
                          >
                            MY PROFILE
                          </Link>
                        </li>
                      ) : (
                        <></>
                      )}
                      {sessionStorage.getItem("token") != null ? (
                        <></>
                      ) : (
                        <>
                          <li className="last">
                            <Link
                            className=" button"
                              to="/contact"
                              style={{  fontSize: "larger" }}
                              // onClick={this.logoutClick}
                            >
                              CONTACT
                            </Link>
                          </li>
                        </>
                      )}

                      {sessionStorage.getItem("token") && (
                        <li
                          style={{
                            display:
                              sessionStorage.getItem("token") !== null
                                ? "block"
                                : "none",
                          }}
                          className="last"
                        >
                          {/* <a
                            onClick={this.logoutClick}
                            style={{ color: "white", fontSize: "large" }}
                          >
                            LOGOUT
                          </a> */}
                          <Link
                          className=" button"
                            to="/"
                            style={{  fontSize: "large" }}
                            onClick={this.logoutClick}
                          >
                            {" "}
                            LOGOUT
                          </Link>
                        </li>
                      )}
                    </ul>
                  </div>
                </nav>
              </div>
              <div className="clearfix"> </div>
            </div>
          </div>
        </div>
      </header>
    );
  }
}
export default Header;
