import React from "react";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

function Footer() {
 
  
  return (
    <footer>
      <ToastContainer/>
      <div className="copy-right"style={{backgroundColor:"#401A0A"}}>
        <div className="container">
          <p>
            &emsp;|&emsp;
            <Link 
            
             to={sessionStorage.getItem("status")==="ACTIVE"&&sessionStorage.getItem("profileStatus") === "ACTIVE"?"/dashboard":"/profile"}
            > Home</Link>&emsp;|&emsp;
            <a href="/terms"> Terms</a>&emsp;|&emsp;
            <a href="/privacyPolicy"> Privacy</a>&emsp;|&emsp;
            <a href="/contact"> Contact Us</a>&emsp;|&emsp;
          </p>
        </div>
        <div className="container">
          <p>
            © {new Date().getFullYear()} All rights reserved | Maintained by{" "}
            <a href="http://sbatsevas.org"> SBAT</a> | Powered By{" "}
            <a href="http://tribandtech.com">Lionorbit</a>
          </p>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
