  changePasswordHandler = (event) => {
    if (
      /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/.test(
        event.target.value
      )
    ) {
      event.target.setCustomValidity("");
    } else {
      event.target.setCustomValidity(
        "Please enter at least 8 characters including upper, lower & special case characters"
      );
    }
    this.setState({ password: event.target.value });
    
    console.log("pass",this.state.password.length,)

  };

  changeConfirmPassword = (e) => {
    

    
    this.setState({ confirmPassword:e.target.value });
   
    
    
  };
  
  

  validate() {
    if (this.form.current.reportValidity()) {
      if (!this.state.captchaApproved) {
        let style = {
          fontSize: "150%",

          padding: "2%",
          marginTop: "90%",
          color: "white",
        };
        toast(
          <div class="container-fluid">
            <div class="row">Invalid Captcha!</div>
          </div>,

          { type: "error", position: "top-center", theme: "colored", style }
        );

        return false;
      }
      if(this.state.password!==this.state.confirmPassword){
        let style = {
          fontSize: "150%",

          padding: "2%",
          marginTop: "90%",
          color: "white",
        };
        toast(
          <div class="container-fluid">
            <div class="row">Invalid Password!</div>
          </div>,

          { type: "error", position: "top-center", theme: "colored", style }
        );
        return false;
      }
      if(this.state.password===this.state.confirmPassword){
        this.setState({ submitted: true });
        let loginData = {
          encodedId: this.state.encodedId,
          password: this.state.password,
        };
        LoginService.resetPassword(loginData)
        .then((res) => {
         
         if(res.data){
          window.location.pathname=("/")
          
         }
         

         
           
        })
        .catch((res) => {
          this.setState({
            submitted: "",message1:""
          });
                          })
      
        return false;
      }
     
     
    }
  }

