import React from "react";
import {Alert,} from 'antd'
const onClose = (e) => {
  console.log(e, 'I was closed.');
};
function SoulMateDiv() {
  return (
    <>
    <div
        className="w3l_find-soulmate text-center "
        style={{ backgroundColor:"#E5D2B0", paddingBottom: "1%" }}
      >
        <h3
          style={{
            letterSpacing: "2px",
            color: "darkorange",
          }}
        >
          Find Your Soulmate
        </h3>
        <div className="container " style={{width:"900px",}}>
          <a className="scroll" href="/">
            <div className="col-md-4 w3_soulgrid" style={{ width: "31%",backgroundColor:"darkorange" }}>
              <i className="fa fa-pencil-square-o"style={{color:"white"}} aria-hidden="true"></i>
              <h3 style={{color:"white"}}>Sign Up</h3>
              <p style={{color:"white"}}>upload your profile</p>
            </div>
          </a>
          <a className="scroll" style={{color:"darkorange"}} href="/">
            <div className="col-md-4 w3_soulgrid"  style={{ width: "31%",background:"#e2e9ef",color:"darkorange" }}>
              <i className="fa fa-search"  style={{color:"darkorange"}}aria-hidden="true"></i>
              <h3  style={{color:"darkorange"}}>Search</h3>
              <p  style={{color:"darkorange"}}>your right partner</p>
            </div>
          </a>
          <a className="scroll" href="/">
            <div className="col-md-4 w3_soulgrid" style={{ width: "31%",background:"#401A0A" }}>
              <i className="fa fa-users"style={{color:"white"}} aria-hidden="true"></i>
              <h3 style={{color:"white"}}>Connect</h3>
              <p style={{color:"white"}}>with your perfect match</p>
            </div>
          </a>
        </div>
      </div>
     
     
    </>
  );
}

export default SoulMateDiv;
