import React, { Component } from "react";
import "antd/dist/antd.css";
import { message, Popconfirm, Button, Space } from "antd";
import InterestService from "../../services/InterestService";
import ListingService from "../../services/ListingService";
import Profile from "../../profile";
import { ToastContainer, toast } from "react-toastify";
import {Link} from 'react-router-dom'
import AlertBox from "./Notification";
import Loding from "./spin";

class NotInterestBtn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clicked: false,
      loadable: false,
      popupMsg: false,
      submitted: false,
      notsubmitted: false,
      userId: "",
      spin:false
    };
    // this.onSubmit = this.onSubmit.bind(this);
    // this.onClick = this.onClick.bind(this);
  }

  onSubmit=(e)=> {
    window.location.path = "/dashboard";
    this.setState({
      popupMsg: false,
    });
    let notinterest = {
     
      interestId: this.props.profileUserId,
      fromUser: sessionStorage.getItem("userId")
     
    };
    InterestService.removeinterest(notinterest)
      .then((result) => {
        if(result){
        window.location.pathname=("profile/"+this.props.profileProfNum)
        const style = {
          fontSize: "20px",
          padding: "2%",
          marginTop: "70%",
          backgroundColor: "orange",
          color: "white",
        };
    
        toast(
          <div class="container-fluid">
            <div class="row">We've notified the user</div>
          </div>,
    
          { position: "top-center",hideProgressBar: true,limit:1, style }
        );
        }
      
        this.setState({
          loadable: false,
          clicked: false,
          submitted: true,
        });

        // message.success("We've notified the user about your interest! ");
        
      })

      .catch((error) => {
        let style = {
          fontSize: "150%",
          
          padding: "5%",
          marginTop: "90%",
          color: "white",
        };

        toast(
          <div class="container-fluid">
          <div class="row">
          Something went wrong! Please try again!
          </div>
        </div>,
         {
          type: "error",
          position: "top-center",
          theme: "colored",
          style,
        });
        // message.error("Something went wrong! Please try again!");
        this.setState({
          loadable: false,
          clicked: false,
        });
      });
  }

  onClick=(e)=> {
    // let viewer = this.props.profileUserId;
    // let id = sessionStorage.getItem("userId");
    // e.preventDefault();
    // this.setState({
    //   clicked: true,
    //   loadable: true,
    // });
    if (
      this.props.status !== "INACTIVE" &&
      sessionStorage.getItem("status") !== "INACTIVE"
    ) {
      this.setState({
        loadable: false,
      });
      this.onSubmit();
      this.setState({spin:true})
    } else {
      this.setState({
        loadable: false,
        clicked: false,
      });
      if (sessionStorage.getItem("status") === "INACTIVE") {
        let style = {
          fontSize: "150%",
          width: "150%",
          padding: "5%",
          marginTop: "90%",
          color: "white",
        };

        toast(
          <div class="container-fluid">
  <div class="row">
  This needs your profile to be activated first!...
  </div>
</div>
          ,//<div class="">This needs your profile to be activated first!</div>,
          { type: "warning", position: "top-center", theme: "colored", style }
        );
      } else {
        let style = {
          fontSize: "150%",
          width: "150%",
          padding: "5%",
          marginTop: "90%",
          color: "white",
        };
        toast(
          <div class="">
            This user hasn't activated the profile yet. You might have to wait
            till then!
          </div>,
          { type: "warning", position: "top-center", theme: "colored", style }
        );
      }
    }
  }

  render() {
    console.log("this.props.profileUserId", this.toUserId);
    console.log(" this.props.profileProfNum", this.toProfileNumber);
    return (
      <>
        <ToastContainer />
        <Popconfirm
          visible={this.state.popupMsg}
          title={
            "Your profile doesn't match with this person's preferences. Do you wish to continue?"
          }
          onConfirm={this.onSubmit}
          onCancel={(e) =>
            this.setState({
              popupMsg: false,
              loadable: false,
            })
          }
          okText="Yes"
          cancelText="No"
        >
          <div style={{ display: "flex" }}>
            <Space>
              {/* <Button
              
                loading={this.state.loadable}
                type="primary"
                disabled={this.state.submitted}
                onClick={this.onClick}
              >
                {!this.state.submitted ? (
                  <>Remove Interest</>
                ) : (
                  <>Interest Removed</>
                )}
              </Button> */}
              {!this.state.submitted ? (
                  <> <AlertBox
                  callback={this.onClick}
                  spin={this.state.spin?<Loding/>:""}
                  title="Remove Interest"button="Remove Interest"
                  content="Do you wish to Remove Interest for this profile ?"/>
</>
                ) : (
                  <><Button type="primary"
                  disabled={this.state.submitted}>Interest Removed</Button></>
                )}
                         </Space>
          </div>
        </Popconfirm>
      </>
    );
  }
}

export default NotInterestBtn;
