
import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';

import { Modal, Button, Space, Input } from 'antd';
let input =<Input/>
class AlertBox extends React.Component {
  constructor(props){
    super(props)
  }
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
    this.props.callback()
   
    
    // window.location.pathname=("/checkout")
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <div>
      
        <Button  style={{backgroundColor:"#401A0A",color:"white"}} onClick={this.showModal}>
        <Space >
        <span className=''>{this.props.button}</span>
        <span>{this.props.spin}</span>

        </Space>
        
        </Button>
        <Modal
     
          title={<div style={{color:"#401A0A"}}>{this.props.title}</div>}
          visible={this.state.visible}
          secondary
          footer={null}
        //  onOk={this.handleOk,<div style={{backgroundColor:"red"}}></div>}
          onCancel={this.handleCancel}
         
        >
         
         
         <div>
         {this.props.content !==""?<p className=''
          style={this.props.content !==""?{backgroundColor:"orange",color:"white",fontSize:"16px", padding:"3%",borderRadius:"3%"}:""
         
          }>{this.props.content}
         
          </p>:""}
          
          <p>{this.props.comment}</p>
         <span>{this.props.checkbox}</span> <span style={{color:"#401A0A"}}>{this.props.queation}</span>
         
         {this.props.checkbox?<><hr/><p style={{fontSize:"16px",color:"#401A0A",padding:"3%",fontFamily:"cursive"}}>{this.props.checkBoxbelow}</p></>:""}
         </div>
         <button  className='btn btn-secondary'onClick={this.handleCancel}>Cancel</button>
         <button className='btn   pull-right' style={{width:"64px",backgroundColor:"#401A0A",color:"white",}}onClick={this.handleOk}>Ok</button>
          
        </Modal>
      </div>
    );
  }
}

export default AlertBox
// export default  antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
