import React, { Component, useState } from "react";
import Login from "./components/login";
import Signup from "./components/signup";
import {  message } from "antd";
import SoulMateDiv from "./components/common/soulmateDiv";

import  ProfileService from './services/ProfileService'

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
        active: "",
      show:true
    };
  }
  getActivateUser=async()=>{
    ProfileService. getActivateUsers().then((res)=>{
     this.setState({active:res.data.count})
      console.log(res.data.count)
      console.log(res)
    }).catch((err)=>{
      // console.log("err")
    })
   }
  componentDidMount() {
    
    if (localStorage.getItem("transactionExpired")) {
      message.error("Transaction Expired!");
      localStorage.removeItem("transactionExpired");
    }
    if (this.props.location.state !== undefined) {
      const displaytext = this.props.location.state.message;
      if (displaytext?.toString().indexOf("Session") !== -1) {
        message.error(displaytext);
      } else {
        message.success({
          content: displaytext,
          class: "text-center",
        });
        if (!localStorage.getItem("firstLoad")) {
          //if not reloaded once, then set firstload to true
          localStorage["firstLoad"] = true;

          window.location.reload();
        } else localStorage.removeItem("firstLoad");
      }
    }
  }
//style={{backgroundColor:"red",marginLeft:"700px"}}
  render() {
    
   
    
    return (
      <>
     
     
        <section id="Sign-Up">
        <div className="w3layouts-banner" id="home">
          <div className="container" >
            <div className="clearfix"></div>
            {this.state.show ?<Login message={this.setMessage}register={()=>this.setState({show:false})} />:
           <Signup login={()=>this.setState({show:true})}/>}
            
           
          </div>
         
     
        </div>
        </section>
        <section>
        <a href="/Sign-Up"> <SoulMateDiv  register={()=>this.setState({show:false})}/></a>
   
          </section>    
      </>
    );
  }
}
export default Home;
