import React, { Component } from "react";
import "antd/dist/antd.css";
import { message, Popconfirm, Button, Space } from "antd";
import InterestService from "../../services/InterestService";
import ListingService from "../../services/ListingService";
import Profile from "../../profile";
import { ToastContainer, toast } from "react-toastify";
import { Link } from "react-router-dom";
import AlertBox from "./Notification";
import Spin from "./spin";
import Loding from "./spin";
import "react-toastify/dist/ReactToastify.css";
// import AlertPopUp from "./alert";
// import AlertPopUp, { tostify } from "./alert";

class InterestBtn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clicked: false,
      loadable: false,
      popupMsg: false,
      submitted: true,
      notsubmitted: false,
      userId: "",
      spin: false,
    };
    // this.onSubmit = this.onSubmit.bind(this);
    // this.onClick = this.onClick.bind(this);
    // this.tostify=this.tostify.bind(this)
  }
  tostify() {
    const style = {
      fontSize: "20px",
      padding: "2%",
      marginTop: "70%",
      backgroundColor: "orange",
      color: "white",
    };

    toast(
      <div class="container-fluid">
        <div class="row">We've notified the user</div>
      </div>,

      { position: "top-center",hideProgressBar: true,limit:1, style }
    );
    // let customId = "custom-id-yes";
    // let style = {
    //   fontSize: "150%",

    //   padding: "2%",
    //   marginTop: "90%",
    //   color: "white",
    // };

    // toast(
    //   <div class="container-fluid">
    //     <div class="row">We've notified the user !</div>
    //   </div>,

    //   {
    //     type: "success",
    //     position: "top-center",
    //     theme: "colored",
    //     style,
    //     toastId: customId,
    //   }
    // );
  }
  onSubmit=(e)=> {
    let interestDetails = {
      toUserId: this.props.profileUserId,
      fromUserId: sessionStorage.getItem("userId"),
      fromProfileNumber: sessionStorage.getItem("userId"),
      fromEmail: sessionStorage.getItem("userEmail"),
      toProfileNumber: this.props.profileProfNum,
    };

    InterestService.save(interestDetails)
      .then((result) => {
        this.setState({  
          loadable: false,
          clicked: false,
          submitted: false,
          spin: false,
        });
        if (result.data) {
        window.location.pathname=("profile/"+this.props.profileProfNum)
          const style = {
            fontSize: "20px",
            padding: "2%",
            marginTop: "70%",
            backgroundColor: "orange",
            color: "white",
          };
      
          toast(
            <div class="container-fluid">
              <div class="row">We've notified the user</div>
            </div>,
      
            { position: "top-center",hideProgressBar: true,limit:1, style }
          );
       
        }

        console.log("", result);
      })

      .catch((error) => {
        this.setState({
          loadable: false,
          clicked: false,
        });
      });
  }

  onClick=(e) =>{
    if (
      this.props.status !== "INACTIVE" &&
      sessionStorage.getItem("status") !== "INACTIVE"
    ) {
      this.onSubmit();

      this.setState({ spin: true });
    } else {
      this.setState({
        loadable: false,
        clicked: false,
      });
      if (sessionStorage.getItem("status") === "INACTIVE") {
      } else {
        let style = {
          fontSize: "150%",

          padding: "2%",
          marginTop: "90%",
          color: "white",
        };
        toast(
          <div class="">
            This user hasn't activated the profile yet. You might have to wait
            till then!
          </div>,
          { type: "warning", position: "top-center", theme: "colored", style }
        );
      }
    }
  }
  // componentDidUpdate(){
  //   this. onSubmit()
  // }
  render() {
    console.log("this.props.profileUserId", this.toUserId);
    console.log(" this.props.profileProfNum", this.toProfileNumber);
    return (
      <>
      <ToastContainer limit={"1"}/>
        {/* <AlertPopUp
          content="We've notified the user !"
          col="green"
          margin="50%"
        /> */}

        <div style={{ display: "flex" }}>
          <Space>
            {this.state.submitted ? (
              <>
                <AlertBox
                  callback={this.onClick}
                  spin={this.state.spin ? <Loding /> : ""}
                  title="Show Interest"
                  button="Show Interest"
                  disabled={this.state.submitted}
                  content="You have expressed interest for this profile. An email will be sent to this profile. Do you want to continue ?"
                />
              </>
            ) : (
              <>
                <Button type="primary" disabled="true">
                  Interest Sent
                </Button>
              </>
            )}
          </Space>
        </div>
      </>
    );
  }
}

export default InterestBtn;
